.body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  // background-color: #fff;
  // color: #424242;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.wrapper {
  padding: 30px 50px;
}

.input {
  border-color: rgb(178, 178, 189);
  display: block;
  padding: 6px;
  margin: 5px 10px 5px 5px;
  width: 350px;
  border-radius: 3px;
  font-size: 16px;
  border: solid 2px rgb(220, 220, 228);
}

.input--bank {
  border: solid 2px rgb(220, 220, 228);
}

.btn {
  padding: 5px 10px;
  margin: 5px 5px 20px 5px;
  border-radius: 17px;
  border: solid 2px rgb(220, 220, 228);
}

.accounts {
  display: flex;
  padding-bottom: 10px;
  margin-bottom: 40px;
  border-bottom: solid 1px rgb(220, 220, 228);
}

.accounts--bank {
  display: flex;
  padding-bottom: 10px;
}

.label {
  margin-left: 6px;
  font-weight: 500;
}

.title__data {
  padding-top: 20px;
  border-top: solid 1px rgb(220, 220, 228);
}

.client--bank {
  margin-bottom: 20px;
  border-bottom: solid 1px rgb(220, 220, 228);
}
